function OpenLinear() {
	generalState.bg = gfx['bg-2'];
	generalState.bgScale = 1;
	generalState.drawBackground = drawBackground;	
	Object.assign(this, generalState);
	this.envFunction = linearEnvironment;
	
	this.earlySetup = function() {
		generalState.bg = gfx['bg-2'];
		generalState.bgScale = 1;
		generalState.drawBackground = drawBackground;
	}

	this.particularSetup = function() {

		this.song = mgr.sfx.soso;
		this.songVol = .4;

		if (this.sceneArgs.step == 0) player.assignQuest(FlyQuest);
		if (this.sceneArgs.step == 1) player.assignQuest(CrabQuest);

	    /* ~ bugs ~ */

	    // make an fly!
	    bugIndices = [];
		if (this.sceneArgs.step == 0) {
			bugIndices.push(3);
			bugIndices.push(6);
			bugIndices.push(15);
			bugIndices.push(-3);
			bugIndices.push(-6);
			bugIndices.push(-15);
		} else {
			for (let i = 0; i < 10; i++) {
				subtract = lilies.negativeLilies ? 25 : -startLily;
				bugIndex = Math.floor(Math.random() * 50) - subtract;

				if (bugIndices.indexOf(bugIndex) < 0 && bugIndex != startLily) {
					bugIndices.push(bugIndex);
				}
			}
		}
		for (const bugIndex of bugIndices) {
			bugs.push(new Bug(lilies, gfx['fly'], bugIndex));
		}
	}
}